@import '../../../styles/vars.css';

.block {
  z-index: 94;
  text-align: left;
  width: 310px;
  position: absolute;
  left: -320px;
  top: 50%;
  transform: translateY(-50%);
  transition: left 0.7s ease;
}
.block.visible {
  left: 220px;
}

.item {
  margin: 8px 5px;
}

.label, img {
  display: inline-block;
  vertical-align: middle;
}
.label {
  width: calc(100% - 70px);
  margin-left: 8px;
}