@import '../../../styles/vars.css';

.container {
  position: relative;
  background-color: var(--bg-scroll);
  box-sizing: border-box;
}

.container.light {
  background-color: var(--bg-scroll-light);
}

.space {
  position: absolute;
  top: var(--arrow-width);
  bottom: var(--arrow-width);
  background-color: transparent;
}

.vertical {
  width: var(--arrow-width);
  height: 100%;
}
.vertical .anchor {
  position: absolute;
  width: var(--arrow-width);
}
.horizontal {
  height: var(--arrow-width);
  width: 100%;
}
.horizontal .anchor {
  height: var(--arrow-width);
}

.firstArrow {
  position: absolute;
  top: 0;
  left: 0;
}
.secondArrow {
  position: absolute;
  right: 0;
  bottom: 0;
}

.anchor {
  touch-action: none;
}

.disabled .space {
  display: none;
}
