@import '../../../styles/vars.css';

.checkbox {
  border: 1px solid var(--border-checkbox);
  box-sizing: border-box;
  background-color: var(--fill-white);
  margin: 0;
  font-size: 18px;
  outline: none;
  width: 20px;
  height: 20px;
  vertical-align: bottom;
}

.input:disabled {
  opacity: 0.8;
  cursor: default;
}
