@import '../../../styles/vars.css';

.block .header {
  background-color: var(--fill-dark-gray);
  width: 100%;
  height: 33px;
  box-sizing: border-box;
  color: #fff;
  font-family: TradeGothic;
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 3px;
  padding: 0 calc(3 * var(--paddings));
}

.location_left .header {
  /*padding-top: 3px;*/
}

.block {
  background-color: var(--bg-block);
  border: 1px solid var(--fill-dark-gray);
  position: relative;
}

.block:before {
  content: '';
  position: absolute;
  top: calc(0 * var(--paddings));
  right: calc(0.5 * var(--paddings));
  left: calc(var(--paddings));
  bottom: calc(var(--paddings));
  border: 1.5px solid var(--border-light-grey);
  box-sizing: border-box;
  background: none;
  pointer-events:none;
}

.content {
  padding: 12px 7px;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  color: var(--white);
}

.location_left .content {
}

.location_top {
  border-top: none;
  border-left: none;
}

.location_top .header {
  text-align: left;
}

.location_top:before {
  border-left: none;
  border-top: none;
  border-bottom: 1.5px solid var(--border-light-grey);
}

.location_left {
  border-bottom: none;
  border-left: none;
}

.location_left:before {
  border-left: none;
  border-bottom: none;
  border-top: 1.5px solid var(--border-light-grey);
}