@import '../../../styles/vars.css';

.block {
  width: 537px;
}
.block.blockShort {
  width: 495px;
}

.container {
  display: flex;
  flex-direction: row;
  text-align: center;
}

.container > p {
  text-align: left;
}

.leftCol {
  flex-basis: 45%;
}
.drillButtons {
  width: 125px;
  white-space: nowrap;
  margin-bottom: 5px;
}

.block .scrollBlock {
  height: 187px;
  color: #000;
  text-align: left;
  margin-left: 6px;
}

.directionHeader {
  font-weight: bold;
}

.buttonN,
.buttonS {
  display: block;
}
.buttonN {
  margin-bottom: 5px;
}
.buttonW,
.buttonE {
  vertical-align: middle;
}

.buttonsVertical {
  display: inline-block;
  vertical-align: middle;
}
