.container {
  width: 100%;
  height: 100%;
}

.app {
  text-align: center;
  position: relative;
  display: inline-block;
  transform-origin: 0 0;

  overflow: hidden;
}

.top-block {
  width: 1280px;
  height: 237.5px;
  display: flex;
  flex-direction: row;
}

.bottom-left-block {
  width: 220px;
  vertical-align: top;
  display: flex;
  flex-direction: column;
  position: relative;
}
.bottom-right-block {
  width: 1060px;
}

.bottom-block {
  width: 1280px;
  height: 662.5px;
  display: flex;
  flex-direction: row;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}