@import '../../../styles/vars.css';

.container {
  position: relative;
  background-color: var(--bg-scroll);
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: 1px solid var(--border-input);
  box-sizing: border-box;
}
.container.light {
  border-color: var(--border-input-lighter);
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: var(--arrow-width);
  background-color: var(--bg-scroll-block);
  padding: 2px 7px;
  font-size: 18px;
  overflow: hidden;
}
.content.white {
  background-color: var(--white);
}

.contentInner {
  position: absolute;
}
.contentInnerWide {
  width: 100%;
}

.container .scroll {
  position: absolute;
  right: -1px;
  top: -1px;
  bottom: -1px;
  height: auto;
}