@import '../../../styles/vars.css';

.container{
  position: relative;
}

.display{
  font-size: 18px;
  height: var(--arrow-width);
  box-sizing: border-box;
  border: 1px solid var(--border-input);
  background-color: var(--fill-white);
  padding-left: 7px;
  letter-spacing: 1.5px;
  color: #000;
  text-align: left;
  overflow: hidden;
}

.select{
  z-index: 1000;
  font-size: 14px;
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
  border: 1px solid var(--border-input);
  width: calc(100% - var(--arrow-width) + 1px);
  position: absolute;
  top: calc(var(--arrow-width) - 1px);
  box-sizing: border-box;
  background-color: var(--fill-white);
}
.select.upward {
  top: auto;
  bottom: calc(var(--arrow-width) - 1px);
}

.select > li > button {
  text-align: left;
  border: none;
  background: none;
  margin: 0;
  padding: 4px 7px 2px;
  width: 100%;
  height: 100%;
  outline: none;
  letter-spacing: 1.5px;
  color: #000;
}

.select > li > button:not(.bold):hover, .select > li.selected > button{
  background-color: var(--bg-select);
}

.container .arrow{
  position: absolute;
  right: 0;
  top: 0;
}

.item{

}

.expanded .select{
  display: block;
}

li.item .bold {
  font-weight: bold;
}