@import '../../../styles/vars.css';

.block {
  flex-grow: 1;
  z-index: 98;
}

.container {
  text-align: left;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.line {
  margin-bottom: var(--paddings);
  display: flex;
  flex-direction: row;
  line-height: 150%;
  align-items: center;
}
.line > span {
  font-size: 18px;
  flex-grow: 1;
  min-width: 171px;
}

.input {
  flex-grow: 1;
  min-width: 171px;
}

.icon {
  display: inline-block;
  width: 25px;
  height: 25px;
  background-size: contain;
  background: no-repeat center;
  margin-right: 10px;
  flex-shrink: 0;
}

.user {
  background-image: url(./images/user.svg);
}

.buttonIcon {
  border: 1px solid #424242;
  padding: 4px;
  background-color: #555252;
  position: relative;
}

.buttonContainer {
  height: 25px;
  width: 25px;
  margin-right: 10px;
  flex-shrink: 0;
}

.buttonIcon:before {
  content: '';
  position: absolute;
  top: calc(0.7 * var(--paddings));
  right: calc(var(--paddings));
  width: calc(100% - 2 * var(--paddings));
  height: calc(100% - 2 * var(--paddings));
  border: none;
  border-top: 1px solid var(--border-light-gray);
  border-right: 1px solid var(--border-light-gray);
  box-sizing: border-box;
}