@import '../../../styles/vars.css';

.block {
  width: 537px;
}
.block.blockShort {
  width: 495px;
}

.container {
  /*display: flex;
  flex-direction: row;
  text-align: center;*/
}

.container_small {
  margin: 10px auto;
}

.container_small > small, /*.container_small */.dropdown, .container_small > .button {
  display: inline-block;
  vertical-align: middle;
  margin: auto 10px;
}
.container_small > small {
  /*width: 40%;*/
}
.container_small .dropdown {
  width: 200px;
}
.container_small > .button {
  width: 80px;
}

.container_small > .button.wide {
  width: 95px;
}

.abort {
  margin: 10px auto;
}
.abort, .downloadButton {
  padding: 0 7px;
}
