.scrollBlock {
  height: 164px;

  text-align: left;
  width: 80%;
  margin: 10px auto;
}
.scrollAnalysis {
  background-color: var(--bg-scroll-block);
  border: 1px solid var(--border-input);
  font-size: 18px;
}
.scrollInterview {
  height: 100%;
  text-align: left;
  width: 100%;
  margin: auto;
}

.scrollDocs {
  height: 790px;

  text-align: left;
  width: calc(100% - 20px);
  margin: 10px 10px auto;
}
.documentsContainer .dropdown {
  width: 230px;
}
.documentsContainer > .button {
  display: inline-block;
  vertical-align: middle;
}

.groupsRow {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.group {
  flex: 1;
}

.group > span {
  font-size: 18px;
  display: inline-block;
  vertical-align: middle;
}
.pageCounter {
  background: var(--white);
  border: 1px solid var(--black);
  width: 50px;
  margin: auto 5px;
}
.stepBackward, .stepForward {
  display: inline-block;
  vertical-align: middle;
}

.arrowElement {
  margin: 5px auto;
}
.arrowRight {
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;

  border-left: 10px solid #555252;
}
.arrowLeft {
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;

  border-right: 10px solid #555252;
}

.scrollBudget {
  height: 450px;

  text-align: left;
  width: calc(100% - 20px);
  margin: 10px 10px auto;
}
.budgetList > div {
  width: 100%;
}
.budgetList > div > div {
  margin: 1px;
  display: inline-block;
}
.budgetList > div > div:first-child {
  width: 15%;
}
.budgetList > div > div:nth-child(2) {
  width: 20%;
}
.budgetList > div > div:nth-child(3) {
  width: 60%;
}

.container_tiny {
  margin: 20px auto;
  width: 60%;
}

.leftPanel, .rightPanel {
  display: inline-block;
  vertical-align: top;
  width: 47%;
  margin: 1%;
  height: 430px;
}
.buttonInterviewOuter {
  width: 33px;
  height: 30px;
  padding: 4px;
  clear: both;
  float: right;
  margin-right: 15px;
}
.iconBalloon {
  background-image: url(../../../styles/icons/speech-balloon.png);
  width: 1.1em;
  height: 1em;
  display: inline-block;
  background-size: contain;
  background-repeat: no-repeat;
}

.interviewMedia {
  width: 320px;
  border: 1px solid #cdcdcd;
  display: inline-block;
}
.interviewMedia img {
  width: 100%;
  height: auto;
}

.answer, .question {
  border-radius: 10px;
  margin: 6px 8px;
  padding: 8px;
  width: 85%;
}
.answer {
  background-color: #CEF0F2;
  float: right;

}
.question {
  background-color: #CEF2D2;
  float: left;
}

.instructions {
  margin-bottom: 15px;
}

.personInfo {
  font-weight: bold;
  font-size: 1.2em;
  margin-top: 12px;
}