@import '../../../styles/vars.css';

.slider_container {
  user-select: none;
  height: var(--slider-container-height);
  display:flex;
  flex-direction:row;
  align-items: center;
  justify-content: space-around;
  position: relative;
  width: 100%;
}

.slider_bg {
  height: var(--bar-height);
  position: relative;
  display: inline-block;
  vertical-align: middle;
  flex: 1;
  -ms-flex: 1 1 auto;
  -webkit-flex: 1;
  margin: calc(var(--slider-container-height) * 0.5 - var(--bar-height)) 3% calc(1em + var(--handle-height) * 0.8) 3%;
}

.slider_container:focus, .slider_progress:focus{
  outline: none;
}

.slider_progress {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 3px;
}

.slider_handle {
  position: absolute;
  cursor: pointer;
  width: 0;
  height: 0;
  margin-top: calc(-0.5 * var(--handle-height));
  transform: translate(-50%, -50%);
  margin-left: calc(-0.5 * (var(--handle-width) + 2 * var(--handle-border-width)));
  border-width: var(--handle-height) var(--handle-width);
  border-color: transparent;
  border-bottom-color: #ddd;
  border-style: solid;
  touch-action: none;
}

.slider_tick_container {
  line-height: 1em;
  font-size: 0.87em;
  list-style: none;
  position: absolute;
  bottom: calc(-1.1 * var(--handle-height));
  width: 100%;
  left: 0;
  margin: 0;
  padding: 0;
  top: calc(1em + var(--handle-height) * 0.8);
}

.slider_tick {
  position: absolute;
  bottom: 0;
  transform: translateX(-50%);
  cursor: pointer;
}
