@import '../../../styles/vars.css';

.input {
  border: 1.2px solid var(--border-input);
  box-sizing: border-box;
  background-color: var(--fill-white);
  margin: 0;
  font-size: 18px;
  outline: none;
  letter-spacing: 1.5px;
}

.input:disabled {
  opacity: 0.8;
  cursor: default;
}
