@import '../../../styles/vars.css';

.block .staticBlock {
  height: 155px;/*132px;*/
  /*margin-top: 6px;*/
  color: var(--white);
  text-align: left;
  font-size: 18px;
  margin-left: 5px;
}

.block.furtherBlock .scrollBlock {
  height: 180px;
  color: #000;
  text-align: left;
  width: 365px;
  display: inline-block;
  vertical-align: top;
  margin: 0 6px 6px;
}

.block.initBlock {
  width: 300px;
}
.block.furtherBlock {
  width: 981px;
}

.dropdownSublocation {
  width: 50%;
  display: inline-block;
  vertical-align: middle;
  margin-left: 20px;
}
.dropdown {
  margin-bottom: 6px;
}


.optionalBlock {
  height: 33px;
  width: 100%;
  text-align: left;
}


.simpleBlock, .imgBlock, .imgBlock .firstPic, .imgBlock .secondPic {
  display: inline-block;
}
.simpleBlock {
  width: 140px;
  vertical-align: top;
}
.imgBlock {
  width: 446px;
  vertical-align: top;
}
.imgBlock .firstPic, .imgBlock .secondPic {
  width: 210px;
  margin: 0 5px 5px;
  border: 1px solid #5d5d5d;
  height: 136px;
  vertical-align: middle;
}
.imgBlock img {
  width: 100%;
  height: auto;
}

.smallTip {
  font-size: 18px;
  margin: 15px auto 25px auto;
}

.button {
  padding: 1px 7px;
}