@import '../../../styles/vars.css';

.container {
  display: flex;
  flex-direction: row;
  text-align: center;
}

.container > p {
  text-align: left;
}

.leftCol {
  flex-basis: 45%;
}

.block .scrollBlock {
  height: 140px;
  color: #000;
  text-align: left;
}

.bottomBlock {
  margin: 7px;
  text-align: left;
}

.beginButton {
  display: inline-block;
  vertical-align: middle;
  padding: 1px 7px;
}

.block.furtherBlock .scrollBlock {
  height: 180px;
  color: #000;
  text-align: left;
  width: 468px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 6px 6px;
}
.block.furtherBlock img {
  width: 225px;
  vertical-align: top;
  margin: 0 6px 6px;
}

.block.initBlock {
  width: 400px;
}
.block.furtherBlock {
  width: 881px;
}

.text {
  display: inline-block;
  vertical-align: middle;
  font-size: 20px;
  margin-right: 20px;
}

.navBlock {
  display: inline-block;
  width: 91px;
  vertical-align: middle;
}
.navBlock .button {
  display: block;
  margin: 6px;
  width: 100px;
}