@import '../../../styles/vars.css';

.button {
  border: 1px solid var(--border-dark-gray);
  box-sizing: border-box;
  background-color: var(--fill-dark-gray);
  margin: 0;
  position: relative;
  color: var(--white);
  font-size: 24px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  outline: none;
  line-height: 34px;
  padding: 1px 3px 0 0;
}

.button.light {
  border-color: var(--border-light-gray);
  background-color: var(--fill-light-gray);
  color: var(--black);
}

.button:before {
  content: '';
  position: absolute;
  top: calc(0.7 * var(--paddings));
  right: calc(var(--paddings));
  width: calc(100% - 2 * var(--paddings));
  height: calc(100% - 2 * var(--paddings));
  border: none;
  border-top: 1.1px solid var(--border-light-gray);
  border-right: 0.9px solid var(--border-light-gray);
  box-sizing: border-box;
}

.button.light:before {
  border-top-color: var(--border-ligther-gray);
  border-right-color: var(--border-ligther-gray);
}

.button:active {
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
}

.button:disabled {
  opacity: 0.7;
}

.button:disabled:before {
  display: none;
}
