@import '../../../styles/vars.css';

.block {
  flex-grow: 1;
  z-index: 95;
}

.content {
  text-align: left;
  margin-bottom: 11px;
  font-size: 15px;
}

.labels {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 10px;
}

.label {
  font-size: 15px;
  letter-spacing: 0.5px;
  -webkit-font-smoothing: none;
  -webkit-text-stroke: 0;
  text-rendering: optimizeLegibility;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10000;
}