@import '../../../styles/vars.css';

.button {
  width: var(--arrow-width);
  height: var(--arrow-width);
  padding: 0;
}

.arrow {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 0;
  height: 0;
  margin: auto;
}

.up {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 7px solid var(--fill-white);
}
.up.light {
  border-bottom: 7px solid var(--fill-dark-gray);
}

.down {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 7px solid var(--fill-white);
}
.down.light {
  border-top: 7px solid var(--fill-dark-gray);
}

.left {
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-right: 7px solid var(--fill-white);
}
.left.light {
  border-right: 7px solid var(--fill-dark-gray);
}

.right {
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 7px solid var(--fill-white);
}
.right.light {
  border-left: 7px solid var(--fill-dark-gray);
}
