@import '../../../styles/vars.css';

.container {
  padding: var(--paddings);
}

.container_outer {
  display: block;
  overflow: hidden;
}

.container_inner {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.icon {
  position: absolute;
  /*width: 20px;
  height: 20px;*/
  margin-top: -10px;
  margin-left: -10px;
  background-repeat: no-repeat;
  background-size: contain;
}
.icon.icon_sm {
  width: var(--icon-mark-size-sm);
  height: var(--icon-mark-size-sm);
  margin-top: calc(-0.5 * var(--icon-mark-size-sm));
  margin-left: calc(-0.5 * var(--icon-mark-size-sm));
  font-size: 14px;
}
.icon.icon_lg {
  width: var(--icon-mark-size-lg);
  height: var(--icon-mark-size-lg);
  margin-top: calc(-0.5 * var(--icon-mark-size-lg));
  margin-left: calc(-0.5 * var(--icon-mark-size-lg));
  font-size: 18px;
}
.icon.icon_md {
  width: var(--icon-mark-size-md);
  height: var(--icon-mark-size-md);
  margin-top: calc(-0.5 * var(--icon-mark-size-md));
  margin-left: calc(-0.5 * var(--icon-mark-size-md));
  font-size: 16px;
}

.icon_simple {
  pointer-events: none;
}
.white {
  background-image: url(./images/iconWhite.svg);
}
.red {
  background-image: url(./images/icon.svg);
}
.yellow {
  background-image: url(./images/iconYellow.svg);
}
.green {
  background-image: url(./images/iconGreen.svg);
}

.cursor_picking_first {
  cursor: none;
}

.popup {
  white-space: pre;
  position: absolute;
  top: 18px;
  left: 18px;
  border: 1px solid #000;
  background: #f9e6cc;
  z-index: 10;
}

.popup.left {
  left: initial;
  right: 18px;
}
.popup.top {
  bottom: 18px;
  top: initial;
}

.red_dot {
  width: var(--red-dot-width);
  height: var(--red-dot-width);
  margin-left: calc(-0.5 * var(--red-dot-width));
  margin-top: calc(-0.5 * var(--red-dot-width));
  border-radius: calc(0.5 * var(--red-dot-width));
  background: #f00;
  animation: pulse 900ms cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}

.outer {
  transition: transform 200ms linear;
  position: absolute;
}

@keyframes pulse {
  0% { transform: scale(0.6) }
  10% { transform: scale(1) }
  100% { transform: scale(0.6) }
}

.historyArea {
  fill: rgba(255, 0, 0, 0.6);
  stroke: rgba(255, 0, 0, 0.6);
  cursor: pointer;
}

.areaLabel {
  background: red;
  pointer-events: none;
}
.areaLabelBackground {
  fill: #f9e6cc;
  stroke: #000;
  stroke-width: 1px;
  pointer-events: none;
}

.areaVisible {
  opacity: 1;
}
.area {
  opacity: 0;
}

.areaRectFullyVisible {
  opacity: 0.7;
}
.areaRectDim {
  opacity: 0.4;
}