/* Color palette */
:root {
  --white: #fff;
  --black: #000;

  --paddings: 2px;
  --header-height: 120px;
  --tags-color: #939393;

  --border-input: #939191;
  --border-dark-gray: #2A2929;
  --border-dark-grey: #7B7878;
  --border-light-gray: #888686;
  --border-light-grey: #999999;
  --border-ligther-gray: #CAC3C3;
  --border-rg: #930f0f;
  --border-checkbox: #aca9a9;
  --border-input-lighter: #B3B3B3;

  --bg-scroll-light: #E0E0E0;
  --bg-scroll-block: #e2e1e1;
  --bg-scroll: #2e2d2d;
  --bg-select: #c8eac9;
  --bg-block: #6e6e6e;
  --fill-dark-gray: #555252;
  --fill-white: #F5F2F2;
  --fill-light-gray: #B3AAAA;

  --icon-mark-size-sm: 6px;
  --icon-mark-size-md: 15px;
  --icon-mark-size-lg: 30px;

  --arrow-width: 25px;

  --red-dot-width: 10px;

  --handle-width: 9px;
  --handle-height: 15px;
  --handle-border-width: 0;
  --bar-height: 4px;
  --slider-container-height: calc(var(--handle-height) * 2.2);
}
