@import '../../../styles/vars.css';

.button {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  background: transparent no-repeat center;
  background-size: contain;
  width: 100%;
  height: 100%;
  cursor: pointer;
  display: block;
  background-origin: content-box;
}

.icon-list {
  background-image: url(../../../styles/icons/list.svg);
}
.icon-location {
  background-image: url(../../../styles/icons/location.svg);
}
.icon-mode {
  background-image: url(../../../styles/icons/mode.svg);
}
.icon-nav {
  background-image: url(../../../styles/icons/nav.svg);
}
.icon-speech {
  background-image: url(../../../styles/icons/speech-balloon.png);
}
.icon-file {
  background-image: url(../../../styles/icons/file-text.svg);
}
.icon-coin {
  background-image: url(../../../styles/icons/coin-dollar.svg);
}