.modal {
  position: absolute;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /*overflow: auto;*/
}
.invisible {
  visibility: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

.content {
  background-color: #F5F2F2;
  /*margin: 15% auto;*/
  padding: 0;
  border: 2px solid #7B7979;
  width: 50%;

  position: absolute;
  top: -50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: top 0.25s ease-out;
}
.content.wide {
  width: 60%;
}
.content.ultraWide {
  width: 70%;
}
.content.superWide {
  width: 100%;
  height: 100%;
}

.content.centered {
  top: 50%;
}

header {
  background-color: #CECECE;
  /*font-weight: 700;
  font-size:1.5em;*/
  padding: 5px 0;
}
header h2 {
  margin: 0;
}


table {
  width: 90%;
  margin: 5px auto;
}

table > thead,
table > tbody,
table > thead > tr,
table > tbody > tr,
table > thead > tr > th,
table > tbody > tr > td {
  display: block;
}

table > tbody > tr:after,
table > thead > tr:after {
  content: ' ';
  display: block;
  visibility: hidden;
  clear: both;
}

table > tbody {
  overflow-y: auto;
  height: 200px;
}

table > tbody > tr > td,
table > thead > tr > th {
  padding: 3px 1px;
  float: left;
  white-space: nowrap;
}